<template>
    <div class="">
        <el-row :gutter="20">
            <el-col :span="22">
                <div class="">
                    <el-table
                    :data="threadDumpData"
                    border
                    :show-header=false
                    style="width: 100%">
                        <el-table-column
                            prop="value">
                        </el-table-column>
                    </el-table>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import METRIC from '../const/metric'
import req from '../utils/request'
export default {
    data() {
        return {
            threadDumpData: []
        }
    },
    created: function(){
        let _that = this;
        req.requestForm({
            cmd: METRIC.THREAD_DUMP,
            data: '',
            sCallback (res) {
                console.log("========== 请求Thread Dump数据 ==========");
                console.log("请求结果：", res);
                console.log("========== 请求Thread Dump数据 ==========");
                if (res == null) {
                    return;
                }
                let threadDumpData = [];
                for(let i = 0; i < res.length; i++) {
                    let item = res[i];
                    let dumpData = '\"' + item.threadName + '\"' + ' Id=' + item.threadId + ' ' + item.threadState + (item.threadState === 'RUNNABLE' ? '' : ' on ' + item.lockName);
                    
                    if (item.hasOwnProperty('stackTrace')) {
                        for(let j = 0; j < item.stackTrace.length; j++) {
                            let stackTrace = item.stackTrace[i];
                            if (stackTrace) {
                                dumpData = dumpData + '\n' + "at " + stackTrace.className + '.' + stackTrace.methodName + '(' + (stackTrace.nativeMethod ? 'Native Method' : stackTrace.fileName + ':' + stackTrace.lineNumber + ')')
                            }
                        }
                    }
                    threadDumpData.push({
                        value: dumpData
                    });
                }
                _that.threadDumpData = threadDumpData;
            }
        });
    }
}
</script>

