<template>
    <div>
        <el-row :gutter="20">
            <el-col :span="22">
                <!-- TODO: 最近一次更新 -->
                <!-- <p class="modify-hint">Last modified: 5 months ago</p> -->
                <div class="grid-content bg-purple">
                    <textarea name="" id="" rows="25" v-model="brokerConfigs"></textarea>
                </div>
                <div class="reload-btn-box">
                    <button class="reloadBtn" v-on:click="doReloadConfig">Reload config</button>
                    <p class="warn-msg">
                        <span>
                            Be careful,it will reload the config parameters from broker config file permanently.
                        </span>
                    </p>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import METRIC from '../const/metric'
import req from '../utils/request'
export default {
    data() {
        return {
            brokerConfigs: ''
        }
    },
    methods: {
        doReloadConfig() {
            alert("当前不支持！");
        }
    },
    created: function(){
        let _that = this;
        req.requestForm({
            cmd: METRIC.BROKER_CONFIG,
            data: '',
            sCallback (res) {
                console.log("========== 请求Broker Config数据 ==========");
                console.log("请求结果：", res);
                console.log("========== 请求Broker Config数据 ==========");
                if (res == null) {
                    return;
                }
                _that.brokerConfigs = decodeUnicode(res);
            }
        });
    }
}
function decodeUnicode(str) {
    str = str.replace(/\\/g, "%");
    return unescape(str);
}

</script>

