<template>
    <div>
        <el-row :gutter="20">
            <el-col :span="22">
                <div class="grid-content bg-purple">
                    <p class="info-log" v-for="(infoLog, index) in infoLogList" :key='index'>
                        {{infoLog}}
                    </p>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import METRIC from '../const/metric'
import req from '../utils/request'
export default {
    data() {
        return {
            infoLogList: []
        }
    },
    created: function(){
        let _that = this;
        req.requestForm({
            cmd: METRIC.LOGGING,
            data: '',
            sCallback (res) {
                console.log("========== 请求logging数据 ==========");
                console.log("请求结果：", res);
                console.log("========== 请求logging数据 ==========");
                if (res == null) {
                    return;
                }
                _that.infoLogList = res;
            }
        });
    }
}
</script>

