<template>
    <div>
        <el-row :gutter="20">
            <el-col :span="22">
                <div class="broker-item">
                    <h3 style="text-align: center;">Broker</h3>
                    <el-table
                    :data="brokerData"
                    border
                    style="width: 100%">
                        <el-table-column
                            align="center"
                            prop="brokerId"
                            label="Broker Id"
                            width="100">
                        </el-table-column>
                        <el-table-column
                            prop="salve"
                            label="Slave ?"
                            width="100">
                        </el-table-column>
                        <el-table-column
                            label="host"
                            prop="host"
                            width="150">
                        </el-table-column>
                        <el-table-column
                            label="Broker URI"
                            prop="brokerUri">
                        </el-table-column>
                    </el-table>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import METRIC from '../const/metric'
import req from '../utils/request'
export default {
    data() {
        return {
            brokerData: [
                {
                    salve: 'false',
                    brokerUri: 'meta://118.190.236.225:8123',
                    dashboard: 'http://118.190.236.225:8120 '
                }
            ]
        }
    },
    created: function(){
        let _that = this;
        req.requestForm({
            cmd: METRIC.CLUSTER,
            data: '',
            sCallback (res) {
                console.log("========== 请求cluster数据 ==========");
                console.log("请求结果：", res);
                console.log("========== 请求cluster数据 ==========");
                if (res == null) {
                    return;
                }
                let brokerData = [];
                for(let i = 0; i < res.length; i++) {
                    let broker = res[i];
                    brokerData.push({
                        brokerId: broker.id,
                        salve: broker.isSalve ? "true" : "false",
                        host: broker.host,
                        brokerUri: broker.zKString
                    })
                }
                console.log(res.length);
                _that.brokerData = brokerData;
            }
        });
    }
}
</script>

