<template>
    <div>
        <el-row :gutter="20">
            <el-col :span="22">
                <div class="broker-item">
                    <el-table
                    :data="javaProperties"
                    border
                    :show-header=false
                    style="width: 100%">
                        <el-table-column
                            prop="key">
                        </el-table-column>
                        <el-table-column
                            prop="value">
                        </el-table-column>
                    </el-table>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import METRIC from '../const/metric'
import req from '../utils/request'
export default {
    data() {
        return {
            javaProperties: []
        }
    },
    created: function(){
        let _that = this;
        req.requestForm({
            cmd: METRIC.JAVA_PROPERTIES,
            data: '',
            sCallback (res) {
                console.log("========== 请求Java Properties数据 ==========");
                console.log("请求结果：", res);
                console.log("========== 请求Java Properties数据 ==========");
                if (res == null) {
                    return;
                }
                let javaProperties = [];
                for(let k in res) {
                    javaProperties.push({
                        key: k,
                        value: res[k]
                    })
                }
                _that.javaProperties = javaProperties;
            }
        });
    }
}
</script>

