<template>
    <div>
        <el-row :gutter="20">
            <el-col :span="11">
                <div class="grid-content bg-purple">
                    <p class="module-title"><b>Instance</b></p>
                    <el-table
                    :data="instaceData"
                    :show-header=false
                    style="width: 100%">
                        <el-table-column
                            prop="key"
                            width="220">
                        </el-table-column>
                        <el-table-column
                            prop="value">
                        </el-table-column>
                    </el-table>
                </div>
            </el-col>
            <el-col :span="11">
                <div class="grid-content bg-purple">
                    <p class="module-title"><b>System</b></p>
                    <el-table
                    :data="systemData"
                    :show-header=false
                    style="width: 100%">
                        <el-table-column
                            prop="key"
                            width="220">
                        </el-table-column>
                        <el-table-column
                            prop="value">
                        </el-table-column>
                    </el-table>
                </div>
            </el-col>
        </el-row>
        <el-row :gutter="20">
            <el-col :span="11">
                <div class="grid-content bg-purple">
                    <p class="module-title"><b>JVM</b></p>
                    <el-table
                    :data="jvmData"
                    :show-header=false
                    style="width: 100%">
                        <el-table-column
                            prop="key"
                            width="220">
                        </el-table-column>
                        <el-table-column
                            prop="value">
                        </el-table-column>
                    </el-table>
                </div>
            </el-col>
            <el-col :span="11">
                <div class="grid-content bg-purple">
                    <p class="module-title"><b>Broker</b></p>
                    <el-table
                    :data="brokerData"
                    :show-header=false
                    style="width: 100%">
                        <el-table-column
                            prop="key"
                            width="220">
                        </el-table-column>
                        <el-table-column
                            prop="value">
                        </el-table-column>
                    </el-table>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import req from '../utils/request'
import METRIC from '../const/metric'
export default {
    data() {
        return {
            instaceData: [], 
            systemData: [],
            jvmData: [], 
            brokerData: []
        }
    },
    created: function(){
        let _that = this;
        req.requestForm({
            cmd: METRIC.DASHBOARD,
            data: '',
            sCallback (res) {
                console.log("========== 请求dashboard数据 ==========");
                console.log("请求结果：", res);
                console.log("========== 请求dashboard数据 ==========");
                if (res == null) {
                    console.log("【查询dashboard数据异常】");
                    return;
                }
                let instance = res.instance;
                _that.instaceData = [
                    {
                        key: 'Startup',
                        value: caculateStartup(instance.start)
                    }, {
                        key: 'Host',
                        value: instance.host
                    }, {
                        key: 'Data',
                        value: instance.data
                    }, {
                        key: 'Transaction log',
                        value: instance.dataLog
                    }, {
                        key: 'CWD',
                        value: instance.cmd
                    }
                ];
                let systemData = res.system;
                _that.systemData = [
                    {
                        key: 'Physical memory',
                        value: systemData.sysMemory
                    }, {
                        key: 'Physical memory used',
                        value: systemData.sysMemoryUsed
                    }, {
                        key: 'Swap space',
                        value: systemData.swapSpace
                    }, {
                        key: 'Swap space used',
                        value: systemData.swapSpaceUsed
                    }, {
                        key: 'Maximum file descriptors',
                        value: systemData.fileDescriptors
                    }, {
                        key: 'Opened file descriptors',
                        value: systemData.fileDescriptorsUsed
                    }, {
                        key: 'Maximum JVM memory',
                        value: systemData.jvmMemoryMax
                    }, {
                        key: 'Current total JVM memory',
                        value: systemData.jvmMemoryTotal
                    }, {
                        key: 'JVM memory used',
                        value: systemData.jvmMemoryUsed
                    }
                ];
                let jvmData = res.jvm;
                _that.jvmData = [
                    {
                        key: 'Runtime',
                        value: jvmData.runtime
                    }, {
                        key: 'Args',
                        value: jvmData.args
                    }, {
                        key: 'Processors',
                        value: jvmData.processors
                    }
                ];
                let brokerData = res.broker;
                let version = res.version;
                _that.brokerData = [
                    {
                        key: 'Broker Id',
                        value: brokerData.id
                    }, {
                        key: 'Broker URI',
                        value: brokerData.zKString
                    }, {
                        key: 'Version',
                        value: version
                    }
                ];
            }
        })
    }
}

/**
 * 计算服务待机的时间
 */
function caculateStartup (duration) {
    let startup = "";
    let second = Number(duration / 1000).toFixed(0);
    if (second > 60) {
        let minute = Number(second / 60).toFixed(0);
        if (minute > 60) {
            let hours = Number(minute / 60).toFixed(0);
            if (hours > 24) {
                let days = Number(hours / 24).toFixed(0);
                if (days > 7) {
                    let weeks = Number(days / 7).toFixed(0);
                    startup = weeks + ' weeks ago';
                } else if (days > 30) {
                    let months = Number(days /30).toFixed(0);
                    startup = months + ' months ago';
                } else {
                    startup = days + ' days ago';
                }
            } else {
                startup = hours + ' hours ago';
            }
        } else {
            startup = minute + ' minutes ago';
        }
    } else {
        startup = second + ' seconds ago';
    }
    return startup;
}
</script>

