<template>
    <div>
        <el-row :gutter="20">
            <el-col :span="22">
                <div class="broker-item">
                    <h3 style="text-align: center;">Topics (Only contains data)</h3>
                    <el-table
                    :data="topicsData"
                    border
                    style="width: 100%">
                        <el-table-column
                            prop="topic"
                            label="Topic (Click for detail)"
                            width="220"
                            >
                        </el-table-column>
                        <el-table-column
                            align='center'
                            label="Partitions"
                            prop="partition">
                        </el-table-column>
                        <el-table-column
                            align='center'
                            label="Messages Number"
                            prop="messageNumber">
                        </el-table-column>
                        <el-table-column
                            align='center'
                            label="Messages Bytes"
                            prop="messagesBytes">
                        </el-table-column>
                        <el-table-column
                            align='center'
                            label="Average Size (Byte)"
                            prop="averageSize">
                        </el-table-column>
                        <el-table-column
                            align='center'
                            label="Accept Pub"
                            prop="acceptPub">
                        </el-table-column><el-table-column
                            align='center'
                            label="Accept Sub"
                            prop="acceptSub">
                        </el-table-column>
                        <el-table-column
                            fixed="right"
                            label="操作"
                            width="100">
                            <template slot-scope="scope">
                                <el-button @click="deleteTopic(scope.row)" type="text">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import METRIC from '../const/metric'
import req from '../utils/request'
export default {
    data() {
        return {
            topicsData: []
        }
    },
    methods: {
        deleteTopic: function (res){
            let topic = res.topic;
            this.$confirm('此操作将永久删除该Topic, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                // TODO: 删除操作
                console.log("删除topic: ", topic);
                let _that = this;
                req.requestForm({
                    cmd: METRIC.DELETE_TOPIC,
                    data: JSON.stringify({
                        topic: topic
                    }),
                    sCallback(res){
                        console.log("删除Topics 请求结果：", res);
                        if (res === "SUCCESS") {
                            _that.$message({
                                type: 'success',
                                message: '删除成功！'
                            });
                            loadBrokerTopic(_that);
                        } else {
                            _that.$message({
                                type: 'error',
                                message: '删除失败!'
                            });
                        }
                    }
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
        }
    },
    created: function(){
        loadBrokerTopic(this);
    }
}

/**
 * 加载Broker Topic
 * @param _that 对象引用
 */
function loadBrokerTopic (_that) {
    req.requestForm({
        cmd: METRIC.TOPICS,
        data: '',
        sCallback (res) {
            console.log("========== 请求Topics数据 ==========");
            console.log("请求结果：", res);
            console.log("========== 请求Topics数据 ==========");
            if (res == null) {
                return;
            }
            let topicsData = [];
            for(let i = 0; i < res.length; i++) {
                let item = res[i];
                topicsData.push({
                    topic: item.topic,
                    partition: item.partitions,
                    messageNumber: item.messagesNumber,
                    messagesBytes: item.messagesBytes,
                    averageSize: item.averageSize,
                    acceptPub: item.acceptPub + '',
                    acceptSub: item.acceptSub + ''
                })
            }
            _that.topicsData = topicsData;
        }
    });
}
</script>

