import axios from 'axios'

const axiosIns = axios.create({
  baseURL: '/rest',
  timeout: 5000
})

// request interceptor
axiosIns.interceptors.request.use(
  config => {
    // do something before request is sent
    return config
  },
  error => {
    // do something with request error
    console.log(error) // for debug
    return Promise.reject(error)
  }
)

axiosIns.interceptors.response.use(
  response => {
    const status = response.status
    // if the custom code is not 20000, it is judged as an error.
    if (status !== 200) {
      return Promise.reject(new Error('Error'))
    } else {
      return response.data;
    }
  },
  error => {
    console.log('err' + error) // for debug
    return Promise.reject(error)
  }
)

/**
 * 统一请求
 * POST 格式：application/x-www-form-urlencoded
 */
function requestForm (param) {
  const params = new URLSearchParams();
  params.append('cmd', param.cmd);
  params.append('data', param.data);
  axiosIns.post(axiosIns.baseUrl, params)
      .then(function (res) {
          console.log("请求成功：", res);
          param.sCallback && param.sCallback(res);
      })
      .catch(function (err) {
          console.log("【统一异常处理】", err);
      });
}

export default {requestForm}
